import axios from 'axios';
import { useContext, useEffect, useState } from 'react';
import { CategoryContext } from '../context/CategoryContext';

export function SearchCategory() {
    let { setCategory: setItem } = useContext(CategoryContext)

    // console.log(cateCtx)
    //ham hook
    let [search, setSearch] = useState({
        start: 0,
        length: 10,
        keyword: ''
    });

    let [result, setResult] = useState({
        recordsTotal: 0,
        recordsFiltered: 0,
        data: []
    })

    let [success, setSuccess] = useState(false)

    useEffect(() => {
        //goi lai
        find()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [search])

    const find = async () => {
        var config = {
            method: 'post',
            url: 'http://54.150.115.104:8080/api/category/search',
            headers: {
                'Content-Type': 'application/json'
            },
            data: search
        };

        try {
            let response = await axios(config)

            let result = response.data;// ko co gi
            console.log(result)

            setResult(result)
        } catch (error) {
            console.log(error);
        }
    }

    const handleChange = (e) => setSearch({
        ...search,
        [e.target.name]: e.target.value
    })

    const deleteItem = async (id) => {
        var config = {
            method: 'DELETE',
            url: `http://54.150.115.104:8080/api/admin/category/delete?id=${id}`,
            headers: {
                "Authorization": "Bearer " + localStorage.getItem("accessToken")
            }
        };

        try {
            await axios(config)
            find()
            setSuccess(true)
            setTimeout(() => setSuccess(false), 2000)
        } catch (error) {
            console.log(error);
        }
    }

    const back = () => {
        let start = search.start - parseInt(search.length)
        start = start < 0 ? 0 : start
        setSearch({
            ...search,
            start
        })
    }

    const next = () => {
        let start = (search.start) + parseInt(search.length)
        console.log(start)
        if (start < result.recordsFiltered)
            setSearch({
                ...search,
                start
            })
    }

    return (
        <div>
            <h2>Search Category</h2>
            <div>
                <label>Keyword</label>
                <input onChange={handleChange} name="keyword" />

                <select onChange={handleChange} name="length"
                    value={search.length}>
                    <option value={5}>5</option>
                    <option value={10}>10</option>
                    <option value={15}>15</option>
                </select>

                <button type='button' onClick={back}>
                    <i className="fa-solid fa-backward"></i>
                </button>
                <button type='button' onClick={next}>
                    <i className="fa-solid fa-forward"></i>
                </button>
            </div>
            {
                success && <div className='text-success'>
                    <p>Thanh cong!!!!</p>
                </div>
            }

            <table className="table table-striped">
                <thead>
                    <tr>
                        <th>Id</th>
                        <th>Name</th>
                        <th>Option</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        result.data.map(({ id, name }) => (
                            <tr key={id}>
                                <td>{id}</td>
                                <td>{name}</td>
                                <td>
                                    <button className='btn btn-danger' onClick={() => deleteItem(id)}>
                                        <i className="fa-solid fa-trash-can"></i> Delete
                                    </button>
                                    <button className='btn btn-warning' onClick={() => setItem({ id, name })}>
                                        <i className="fa-solid fa-pen-to-square"></i> Edit
                                    </button>
                                </td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
        </div>
    )
}