import axios from 'axios';
import { useContext, useEffect, useState } from 'react';
import { CategoryContext } from '../context/CategoryContext';

const imageLogo = { width: 62 }

export function EditCategory({ setNew }) {
    //ham hook
    let { category, setCategory } = useContext(CategoryContext)

    // useEffect(() => {
    //     if (item)
    //         setCategory(item)
    // }, [item])

    const save = async () => {
        var config = {
            method: 'put',
            url: 'http://54.150.115.104:8080/api/admin/category/update',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": "Bearer " + localStorage.getItem("accessToken")
            },
            data: category
        };

        try {
            let response = await axios(config)

            let result = response.data;// ko co gi
            console.log(result)
            setNew(new Date().getTime())
        } catch (error) {
            console.log(error);
        }
    }

    const handleChange = (e) => setCategory({
        ...category,
        [e.target.name]: e.target.value
    })

    return (
        <div>
            <h2>Update Category</h2>
            <div>
                <label>Id</label>
                <input onChange={handleChange} name="id" value={category?.id} />
            </div>
            <div>
                <label>Name</label>
                <input onChange={handleChange} name="name" />
            </div>
            <div>
                <button type='button' onClick={save}>Update</button>
            </div>
        </div>
    )
}